.App-header {
    background-color: white !important;
    min-height: 12%;
    /* height: 12vh; */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    
    color: white;
  }

