    /* font-size: 50px; */
    /* background-attachment: fixed;
    background-position: center; */
  
    /* -webkit-animation: fadein 2s;
    -moz-animation: fadein 2s; */
  }
  
  .BootstrapNav {
    min-height: 100%;
    font-size: calc(10px + 2vmin);
    background-color: rgb(62, 97, 92) !important;
  }
  .WebsiteTitleLogo{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    font-size: calc(10px + 2vmin);
  }
  .pictureLogo{
    padding-right: 2%;
  }
  .NavLink{
      display: flex;
      align-items: center;
      padding: 0% 2%;
      border: 3px solid #d6b500;
      border-top-style: none;
      border-left-style: none;
      border-right-style: none;
      color: rgb(226, 255, 253);
  }
  .searchForm{
    height: 100%;
  }
  #responsive-navbar-nav{
      display: flex;
      flex-direction: row;
      justify-content: flex-end;
  }
.navbar-collapse{
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
}

@media only screen and (max-width: 995px){
    .navbar-collapse{
        display: flex;
        flex-direction: row;
        justify-content: center;
    }
}
@media only screen and (min-width: 996px){
    .navbar-nav{
        width: 100%;
        display: flex;
        flex-direction: row;
        justify-content: space-around;
    }
}

